@import url(https://fonts.googleapis.com/css2?family=Exo+2:wght@300;400&family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Karla:ital,wght@1,433;1,600&family=PT+Sans+Narrow&family=Titillium+Web:ital,wght@1,200;1,300;1,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

  .column {
    width: 30%;
    margin-left: 40px;
    margin-top: 20px;
    float: center;
    text-align: -webkit-center;
}
.image {
    background-color:rgb(14, 46, 33);
    flex: 1 1;
    position:relative;
    overflow: hidden;
    margin-top:25px;
    margin-bottom: 15px;
    width: 180px;
    height: 180px;  
    border-radius: 10%;
}
.image img {
    opacity: 0.8;
    position: relative;
    vertical-align: top;
    transition: 0.5s;
    transition-property: opacity;
    width: 100%;
    height: 100%;
}

.details p{
    color: black;
    font-weight: bold;
}


@media only screen and (min-width : 320px) {
    .column {
        width: 100%;
        float: left;
    }
}

/* Extra Small Devices, Phones */
@media only screen and (min-width : 480px) {
    .column {
        width: 100%;
        float: left;
    }
}

/* Small Devices, Tablets */
@media only screen and (min-width : 768px) {
    .column {
        width: 50%;
        float: left;
    }
}

/* Medium Devices, Desktops */
@media only screen and (min-width : 992px) {
    .column {
        width: 50%;
        float: left;
    }
}

/* Large Devices, Wide Screens */
@media only screen and (min-width : 1200px) {
    .column {
        width: 30%;
        float: left;
    }
}
body {
    background: linear-gradient(to right,#359c54, #5593b8);
}

@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.wholecontainer {
    background-size: cover;
}

.container {
    height: auto;
}

.topnav {
    height: 70px;
    background-color: rgba(0, 0, 0, 0.500);
    padding: 15px;
    overflow: hidden;
    font-weight: bold;
    display: flex;
    justify-content: space-between;
}

.logo{
    color: white;
    font-size: 55px;
    font-weight: 600;
    justify-content: left;
    margin-left: 30px;
    margin-top: 3px;
    font-family: 'Karla', sans-serif;
    font-family: 'PT Sans Narrow', sans-serif;
    font-family: 'Titillium Web', sans-serif;
}

.topnav .btn {
    display: none;
    float: right;
    display: block;
    text-align: center;
    color: #f2f2f2;
    background-color: rgb(0, 0, 0);
    padding: 10px;
    margin-right: 30px;
    margin-top: 7px;
    text-decoration: none;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 25px;
    font-weight: 400;
    transition: 0.5s;
    border-radius: 5px;
    box-shadow: 0px 0px 0px 1.5px rgb(11, 51, 7);
    width: 10%;
}

.topnav .btn:hover {
    color: black;
    background: transparent;
}

@media screen and (max-width: 600px) {
    .topnav a.btn {
        color: #f2f2f2;
        float: right;
        display: block;
        width: 20%;
    }

    .container {
        height: 100px;
        text-align: -webkit-center;
    }
    .img{
        width: 300px;
        margin-right: 80px;
    }
}

.img {
   margin-top: 30px;
   text-align: center;
}

